@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500;600&display=swap);
.enter{margin-top:4rem;text-align:center}.enter .enter-heading{margin-bottom:20px}.enter .enter-heading h1{font-size:36px;margin-bottom:5px;font-weight:600}.enter .enter-heading p{font-size:16px}.enter .enter-error{color:#f83434;margin-bottom:10px}.enter .enter-form form{display:flex;flex-direction:column;align-items:center}.enter .enter-form form input{height:40px;width:460px;border-radius:5px;padding:0 7px;border:1px solid #B8B8B8;display:block}.enter .enter-form form button{margin-top:15px}.enter .enter-footer{margin-top:25px}@media (max-width: 574px){.enter .enter-form form input{width:100%}}

.header{display:flex;justify-content:space-between;padding:15px 30px;border-bottom:1px solid #F3F3F3}.header .header-title{display:flex;align-items:center}.header .header-title__heading{margin-left:15px}.header .header-title__heading p{font-size:18px;font-weight:600}.header .header-title__heading p span{font-size:14px;font-weight:500}.header .header-tools{display:flex;align-items:center}.header .header-tools span{margin-right:15px;font-weight:600}.header .header-tools svg{margin-right:10px}.header .header-tools svg:last-child{margin-right:0}@media (max-width: 584px){.header{flex-direction:column}.header .header-tools{margin-top:15px}}

.chat{border-radius:7px;border:1px solid #F3F3F3;margin-top:4rem;margin-bottom:2rem;position:relative}.chat .chat-content{height:370px;overflow-y:scroll}.chat .chat-content .info{margin-bottom:15px;font-size:15px}.chat .chat-content .message{margin-bottom:15px}.chat .chat-content .message .message-username p{color:#222222;font-size:15px;margin-bottom:5px}.chat .chat-content .message .message-username span{margin-left:7px;font-size:14px;color:rgba(0,0,0,0.6)}.chat .chat-content .message .message-text{background:#0070F3;box-shadow:0px 0px 5px rgba(0,0,0,0.25);border-radius:0px 7px 7px 7px;color:#fff;display:inline-block;overflow-wrap:anywhere;padding:7px 10px}.chat .chat-online{padding:0 30px;margin-top:15px}.chat .chat-online span{display:flex;justify-content:flex-end;align-items:center;font-size:15px}.chat .chat-online span::before{content:'';display:block;margin-right:10px;border-radius:50px;background:#4ecd13;width:6px;height:6px}.chat .chat-content,.chat .chat-input{padding:0 30px 15px 30px}.chat .chat-input{padding-bottom:25px}.chat .chat-input form{display:flex}.chat .chat-input form input{width:100%;border:none;border-bottom:1px solid #C9C9C9;padding:10px;padding-left:7px;font-size:14px;outline:none}.chat .chat-input form button{background:none;border:none;outline:none;cursor:pointer;border-bottom:1px solid #C9C9C9}.chat .chat-input form button svg{margin-bottom:5px}.chat .chat-input form button:hover svg path{opacity:.8}@media (max-width: 584px){.chat{margin-top:2rem}.chat .chat-content{height:300px}}

html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{font-family:'Inter', sans-serif}h1,h2,h3,h4,h5,h6,p{padding:0;margin:0}::selection{color:#fff;background:#3972f4}::-webkit-scrollbar{width:5px;height:5px}::-webkit-scrollbar-track{border-radius:10px;background:#EFF1F5}::-webkit-scrollbar-thumb{border-radius:5px;background:#597ce4}.container-fluid{max-width:820px;margin-left:auto;margin-right:auto}.btn-primary{height:35px;background:#0070F3;box-shadow:0px 0px 5px rgba(0,0,0,0.25);border-radius:7px;border:none;color:#fff;padding:0 30px;cursor:pointer;transition:all .1s}.btn-primary:hover{background:#2484f3}

